.addKeeper {   
    display: flex;
    flex-direction: column;
    background: #360033;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #0b8793, #360033);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #0b8793, #360033); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    
    padding: 1rem;
    border-radius: 20px;
    margin: 3rem auto;
    width:fit-content;
    box-shadow: rgba(0, 0, 0, 0.5) 10px 10px 20px;
}

.inputBox {
    border: 1px solid #fff;
    border-radius: inherit;
    margin: 0.5rem;
    padding: 1rem;
    background-color:inherit;
    outline: none;
    
}

.inputBox::placeholder {
    color:#fff;
}

.titleInput {
    font-size: 1.5rem;
    color: #fff;
    font-family: 'Texturina', serif;
    
}

.description {
    font-size:1.1rem;
    font-family: 'Kanit', sans-serif;
    min-height: 100px;
    min-width: 400px;
    color: #fff;
    resize: none;
}

.addButton {
    color: black;
    font-weight:bold;
    background: linear-gradient( to bottom right, rgb(114, 248, 185) , rgb(82, 171, 240) );
    margin: 0.5rem auto;
    padding: 0.5rem 4.5rem;
    font-family: 'Texturina', serif;
    border-radius: 20px;
    box-shadow: rgba(50, 50, 93, 0.5) 0px 50px 100px -20px, rgba(0, 0, 0, 0.5) 0px 30px 60px -30px, rgba(10, 37, 64, 0.5) 0px -2px 6px 0px inset;
    cursor: pointer;
}